import components from "./components";
import { version } from "../package.json";
import locale from "./locale";
import axios from "axios";
import { success } from "./libs/request";
//  install  Vue  use 
const install = (Vue, opts = {}) => {
  global.$http = Vue.$axios = Vue.prototype.$http =
    (opts.http && opts.http.axios) || opts.http || axios;
  global.$success = Vue.$success = Vue.prototype.$success =
    (opts.http && opts.http.success) || success;

  // Vue.prototype.$http = opts.axios;
  // 
  if (install.installed) return;
  locale.use(opts.locale);
  locale.i18n(opts.i18n);

  // 
  components.map(component => {
    if (component.name) {
      Vue.component(component.name, component);
    } else {
      console.warn(`'${component.__file}'`);
    }
  });
};

// 
if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}

export default {
  version,
  locale: locale.use,
  i18n: locale.i18n,
  //  install Vue.use() 
  install,
  // 
  ...components
};
